import React, { Component } from 'react';
import axios from 'axios';
import history from './history.js';
import loader from './Eclipse-1s-65px.svg';
import './App.css';

const apiHost = './';

/**
 * Find and highlight mention given a matching pattern within a block of text
 * https://github.com/facebook/react/issues/3386#issuecomment-351757615
 *
 * @param {string} text - The text to parse
 * @param {array} values - Values to highlight
 * @param {RegExp} regex - The search pattern to highlight
 * @return {object} A JSX object containing an array of alternating strings and JSX
 */
const formatText = (text, values, regex) => {
	if (! values.length) {
		return text;
	}

	return (<React.Fragment>
		{text.split(regex).reduce((prev, current, i) => {
			if (! i) {
				return [current];
			}

			return prev.concat(
				values.includes(current) ?
					<i key={i + current}>
						{current}
					</i>
				: current
			);
		}, [])}
	</React.Fragment>);
};

const Loader = (props) => <img id="loader" src={loader} alt="loading" />;

const ButtonSurah = (props) => {
	return (
		<li className="list-surah__item">
			<a href={`QS${props.sid}`}
				className="list-surah__link list-surah__link_action"
				onClick={e => props.onClick({
					action: 'getSurah',
					current: props.sid
				}, e)}>
				<span>{props.sid}</span>
				{props.children}
			</a>
		</li>
	);
};

class ListSurah extends Component {
	componentDidMount() {
		window.scrollTo(this.props.position.left, this.props.position.top);
	}

	render() {
		return (
			<ol className="list-surah flex-list">
			{this.props.suwar.map((surah) =>
				<ButtonSurah
					key={surah.sid}
					sid={surah.sid}
					onClick={this.props.onClick}
				>{surah.name}</ButtonSurah>
			)}
			</ol>
		);
	}
}

class Navigate extends Component {
	render() {
		const {nav, onClick} = this.props,
		prev = nav.prev,
		next = nav.next,
		classes = 'nav-surah__link nav-surah__link_action';

		return (
			<nav className="nav-surah constraint-width">
				<a href={null !== nav.prev ? `QS${prev.sid}` : '#'}
					className={classes}
					style={{visibility: null !== prev ? 'visible' : 'hidden'}}
					onClick={e => onClick({
						action: 'getSurah',
						current: null !== prev ? prev.sid : -1
					}, e)}>
					<span>&laquo;</span> {null !== prev ? prev.name : ''}
				</a>
				<a href="/"
					className={classes}
					onClick={e => onClick({action: 'getSuwar'}, e)}>
					<span>&equiv;</span> Daftar Surat
				</a>
				<a href={null !== next ? `QS${next.sid}` : '#'}
					className={classes}
					style={{visibility: null !== next ? 'visible' : 'hidden'}}
					onClick={e => onClick({
						action: 'getSurah',
						current: null !== next ? next.sid : -1
					}, e)}>
					{null !== next ? next.name : ''} <span>&raquo;</span>
				</a>
			</nav>
		);
	}
}

const Paragraph = (props) => <p className={props.className}>{props.children}</p>;

class ReadSurah extends Component {
	constructor(props) {
		super(props);

		this.state = {
			ayah: [],
			ayahPlain: [],
			selected: [],
			err: null
		};

		this.handleClick = this.handleClick.bind(this);
		this.fetchData = this.fetchData.bind(this);

		this.elCopy = React.createRef();
	}

	componentDidMount() {
		this.fetchData(this.props.read);
	}

	componentDidUpdate(prevProps, prevState, snapshot) {
		if (prevProps.read !== this.props.read) {
			this.fetchData(this.props.read);
		}
	}

	componentWillUnmount() {
		this.props.selected([]);
	}

	handleClick(e) {
		const span = e.target.parentElement,
		verseID = parseInt(span.id.substring(2), 10),
		selected = this.state.selected;
		let verses = [],
		versesNum = [];

		if (span.classList.contains('hilite')) {
			span.classList.remove('hilite');
			selected.splice(selected.indexOf(verseID), 1);
		} else {
			span.classList.add('hilite');
			selected.push(verseID);
		}

		this.setState({selected: selected.sort((a, b) => a - b)});

		if (0 < this.state.selected.length) {
			for (let verse of this.state.selected) {
				versesNum.push(verse);
				verses.push(this.state.ayahPlain[verse - 1]);
			}
			verses.push(`~ ${this.props.read.title}:${versesNum.join(',')}`);
		}

		this.props.selected(verses);
	}

	fetchData(surah) {
		const regex = new RegExp('<i>(.*?)</i>', 'g'),
		rgx = new RegExp('<i>|</i>', 'g'),
		verses = [],
		versesPlain = [];
		let pState = 'sa',
		tmp = [];

		this.setState({
			ayah: [<Loader key="loader" />],
			ayahPlain: [],
			selected: []
		});

		axios(`${apiHost}surah/${surah.file}`).then(result => {
			for (let verse of result.data) {
				if ('0' === verse.aid) {
					verses.push(<Paragraph key={`p-${verses.length}`}>{verse.text}</Paragraph>);
				} else {
					if ('undefined' !== typeof verse.p) {
						pState = verse.p;
					} else {
						pState = 0 < tmp.length ? 'il' : 'sa';
					}

					if ('nl' === pState || 'nlcp' === pState) {
						tmp.push(<br key={`nl-${verse.aid}`}/>);
					} else {
						tmp.push(' ');
					}

					versesPlain.push(verse.text.replace(rgx, ''));
					let values = verse.text.match(regex);
					values = values || [];

					tmp.push(<span key={verse.aid} id={`l-${verse.aid}`}>
						<sup onClick={e => this.handleClick(e)}>&#123;{verse.aid}&#125;</sup>
						{formatText(verse.text, values.map(val => val.replace(rgx, '')), regex)}
					</span>);

					if ('cp' === pState || 'nlcp' === pState || 'sa' === pState) {
						verses.push(<Paragraph key={`p-${verses.length}`}>{tmp}</Paragraph>);
						tmp = [];
					}

					if ('undefined' !== typeof verse.r) {
						verses.push(<Paragraph key={`p-${verses.length}`} className="end-section">
							<span>{`ruku\` ${verse.r}`}</span>
						</Paragraph>);
					}
				}
			}

			verses.unshift(<h4 key="title">{surah.title}<br/>
				<small>{surah.place}, {result.data.length - 1} ayat</small>
			</h4>);

			this.setState({
				ayahPlain: versesPlain,
				ayah: verses
			});

			window.scrollTo(0, 0);

			this.props.selected(this.state.selected);
			this.props.loading(false);
		}).catch(err => {
			this.setState({err});
			this.props.loading(false);
		});
	}

	render() {
		if (this.state.err) {
			return <Paragraph>{this.state.err}</Paragraph>;
		}

		return (
			<section className="read-surah">
				{this.state.ayah}
			</section>
		);
	}
}

const Utility = (props) => {
	return (
		<li className="utilities__item">
			<button onClick={e => props.onClick(e)}>{props.children}</button>
		</li>
	);
};

class Utilities extends Component {
	constructor(props) {
		super(props);

		this.state = {
			utilities: [
				{
					action: 'copy',
					label: 'Salin'
				}
			]
		};

		this.handleClick = this.handleClick.bind(this);

		this.elCopy = React.createRef();
	}


	handleClick(action) {
		switch (action) {
			case 'copy':
				this.elCopy.current.select();
				document.execCommand('copy', false);
				break;

			default:
				break;
		}
	}

	render() {
		if (! this.props.show) {
			return null;
		} else if (this.state.err) {
			return this.state.err;
		}

		return (
			<React.Fragment>
				<ul className="utilities flex-list">
			{this.state.utilities.map((utility) =>
					<Utility
						key={utility.action}
						onClick={() => this.handleClick(utility.action)}
					>{utility.label}</Utility>
			)}
				</ul>
				<textarea ref={this.elCopy} value={this.props.toCopy} readOnly style={{
					position: 'absolute',
					clip: 'rect(0, 0, 0, 0)',
					whiteSpace: 'nowrap',
					border: '0 none'
				}} />
			</React.Fragment>
		);
	}
}

class QuranTranslation extends Component {
	constructor(props) {
		super(props);

		this.state = {
			isSingle: false,
			isBlocking: false,
			showUtil: false,
			panelBottom: 0,
			pos: {
				left: 0,
				top: 0
			},
			ayat: [],
			suwar: [],
			surah: {},
			navigate: {
				current: -1,
				prev: null,
				next: null
			},
			err: null
		};

		this.handleClick = this.handleClick.bind(this);
		this.goBlock = this.goBlock.bind(this);
		this.showUtilities = this.showUtilities.bind(this);

		this.elFixed = React.createRef();
	}

	componentDidMount() {
		this.setState({isBlocking: true});

		axios(`${apiHost}surah/index.json`).then(result => {
			const {data} = result;
			let state = {};

			if ('/' !== history.location.pathname) {
				const current = parseInt(history.location.pathname.substring(3), 10) - 1;
				state = {
					suwar: data,
					isSingle: true,
					surah: {
						title: data[current].name,
						file: data[current].file,
						place: data[current].place
					},
					navigate: {
						current: current,
						prev: 0 < current ? data[current - 1] : null,
						next: 113 > current ? data[current + 1] : null
					}
				};
			} else {
				state = {
					suwar: data,
					isBlocking: false
				};
			}

			this.setState(state);
		}).catch(err => {
			this.setState({err});
			this.setState({isBlocking: false});
		});
	}

	componentDidUpdate(prevProps, prevState, snapshot) {
		if (this.state.isSingle && null !== this.elFixed.current && prevState.panelBottom !== this.elFixed.current.clientHeight) {
			this.setState({panelBottom: this.elFixed.current.clientHeight});
		}
	}


	handleClick(d = {action: ''}, e) {
		e.preventDefault();
		history.push({
			pathname: e.currentTarget.pathname,
			search: e.currentTarget.search
		});

		const {isBlocking, suwar} = this.state;

		switch (d.action) {
			case 'getSurah':
				const current = parseInt(d.current, 10) - 1,
				left = window.pageXOffset || document.documentElement.scrollLeft,
				top = window.pageYOffset || document.documentElement.scrollTop;

				if (0 <= current && 113 >= current && ! isBlocking) {
					this.setState({
						isSingle: true,
						isBlocking: true,
						pos: {left, top},
						surah: {
							title: suwar[current].name,
							file: suwar[current].file,
							place: suwar[current].place
						},
						navigate: {
							current: current,
							prev: 0 < current ? suwar[current - 1] : null,
							next: 113 > current ? suwar[current + 1] : null
						}
					});
				}
				break;

			case 'getSuwar':
				if (! isBlocking) {
					this.setState({
						isSingle: false,
						panelBottom: 0,
						isBlocking: false
					});
				}
				break;

			default:
				break;
		}
	}

	goBlock(tof) {
		this.setState({isBlocking: tof});
	}

	showUtilities(selected) {
		this.setState({
			showUtil: 0 < selected.length,
			ayat: selected
		});
	}

	renderComponents = () => {
		if (this.state.err) {
			return <Paragraph>{this.state.err}</Paragraph>;
		}

		if (this.state.isSingle) {
			return (<React.Fragment>
				<ReadSurah read={this.state.surah} loading={this.goBlock} selected={this.showUtilities} />
				<aside ref={this.elFixed} className="fixed-wrapper">
				{this.state.showUtil ? (
					<Utilities show={this.state.showUtil} toCopy={this.state.ayat.join(' ')} />
				) : (
					<Paragraph className="message">Klik pada nomor ayat untuk memilih.</Paragraph>
				)}
					<Navigate nav={this.state.navigate} onClick={this.handleClick} />
				</aside>
			</React.Fragment>);
		} else {
			if (this.state.isBlocking) {
				return <Loader />;
			} else {
				return <ListSurah suwar={this.state.suwar} position={this.state.pos} onClick={this.handleClick} />
			}
		}
	};

	render() {
		return (
			<main className="app constraint-width" lang="id" dir="ltr">
				<h3>Terjemahan Alquran</h3>
				<div className="panel" style={{marginBottom: this.state.panelBottom}}>
			{this.renderComponents()}
				</div>
			</main>
		);
	}
}

export default QuranTranslation;



